import cityDatas from '@/components/city-data/city.area.js'
import uniPopup from '@/components/city-data/uni-popup.vue'
export default {
	components: {
		uniPopup
	},
	data() {
		return {
			provinceData: cityDatas,
			cityData: [],
			areaData: [],
			selectList: [{
				txt: '请选择'
			}, {
				txt: '请选择'
			}, {
				txt: '请选择'
			}],
			tabOne: '请选择',
			indexTab: 0,
			proviceShow: true,
			areaShow: false,
			cityShow: false,
			show: false,
			type: '',
			checkOne: null,
			checkTwo: null,
			checkThree: null,
			city: [],
			evaluate: '',
			vitae: '',
			mailbox: '',
			sex: '',
			phone: '',
			post: '',
			salary: '',
			state: '',
			name: '',
			Certificates: '',
			length:''
		}
	},
	methods: {
		formSubmit(e) {
			// console.log(e)
			this.Certificates = e.detail.value.Certificates;
			var Certificates = e.detail.value.Certificates;
			this.name = e.detail.value.name;
			this.mailbox = e.detail.value.mailbox;
			this.sex = e.detail.value.sex;
			this.phone = e.detail.value.phone;
			this.post = e.detail.value.post;
			this.salary = e.detail.value.salary;
			this.state = e.detail.value.state;
			var length = Certificates
			if (length==0) {
				uni.showToast({
					title: '请填写完整信息！！！',
					icon: 'fail',
					duration: 1000,
					mask:true
				})
			} else{
				try{
					uni.setStorageSync("name",this.name);
					uni.setStorageSync("city",this.city);
					uni.setStorageSync("evaluate",this.evaluate);
					uni.setStorageSync("vitae",this.vitae);
					uni.setStorageSync("mailbox",this.mailbox);
					uni.setStorageSync("sex",this.sex);
					uni.setStorageSync("phone",this.phone);
					uni.setStorageSync("post",this.post);
					uni.setStorageSync("salary",this.salary);
					uni.setStorageSync("state",this.state);
					uni.setStorageSync("Certificates",this.Certificates);
				}catch(e){
					//TODO handle the exception
				}
					setTimeout(() => {
							uni.switchTab({
								url: "../resume/resume" ,	
							});
						}, 600);
					}

		},
		textareaAInput(e) {
			var vitae = e.detail.value;
			this.vitae = vitae
		},
		textareaBInput(e) {
			var evaluate = e.detail.value;
			this.evaluate = evaluate

		},
		togglePopup(type, open) {
			this.type = type
			if (open === 'tip') {
				this.show = true
			} else {
				this.$refs[open].open()
			}
		},
		cancel(type) {
			if (type === 'tip') {
				this.show = false
				return
			}
			this.$refs[type].close()
		},
		change(e) {
			if (e.show == true) {
				uni.hideTabBar()
			} else {
				uni.showTabBar()
			}
		},
		tabEvent(index) {
			this.indexTab = index
			if (this.indexTab == 0) {
				this.proviceShow = true
				this.cityShow = false
				this.areaShow = false
				// this.checkOne = null
				this.checkTwo = null
				this.checkThree = null
				// this.cityData = []
				this.areaData = []
				// this.selectList[0].txt = "请选择"
				this.selectList[1].txt = "请选择"
				this.selectList[2].txt = "请选择"
			} else if (this.indexTab == 1) {
				this.proviceShow = false
				this.cityShow = true
				this.areaShow = false
				// this.checkTwo = null
				this.checkThree = null
				// this.areaData = []
				// this.selectList[1].txt = "请选择"
				this.selectList[2].txt = "请选择"
			} else if (this.indexTab == 2) {
				this.proviceShow = false
				this.cityShow = false
				this.areaShow = true
			}
		},
		//省级选择
		provinceEvent(data, index) {
			// console.log(data)
			var city = data.name;
			this.city[0] = city;
			this.checkOne = index
			this.selectList[0].txt = data.name
			this.indexTab = 1
			this.proviceShow = false
			this.cityShow = true
			this.areaShow = false
			this.cityData = data.cityList
		},
		// 市级选择
		cityEvent(data, index) {
			var city = data.name;
			this.city[1] = city;
			this.checkTwo = index
			this.selectList[1].txt = data.name
			this.indexTab = 2
			this.proviceShow =	 false
			this.cityShow = false
			this.areaShow = true
			this.areaData = data.areaList
		},
		// 地区级
		areaEvent(data, index) {
			var area = data.name;
			this.city[2] = area;
			console.log(this.city[2])
			this.checkThree = index
			this.selectList[2].txt = data.name
		}
	}
}
